* {
    margin: 0;
}

div{
    border: 2px solid;
    font-size: 2em;
}

main{
    display: grid;
    height: 100vh;
    grid-template-columns: repeat(3, 100px);
    grid-template-rows: repeat(3, 100px);
    gap: 3px;
    justify-content: center;
    align-content: center;
    //grid-auto-flow: row;
    //grid-auto-columns: 100px;
    grid-auto-flow: column;
    grid-auto-columns: 100px;
}